const { STORAGE_COPY_COUNT, WARNING_TEXT } = require('../data/index')
const Config = '../config/index'

// 
const additionData = function(event){
    event.preventDefault()
    const selection = window.getSelection(0).toString()
    const textData = selection + '\n' + WARNING_TEXT
    if(event.clipboardData){
        event.clipboardData.setData('text/plain', textData);
    }
    else if(window.clipboardData){
        return window.clipboardData.setData("text", textData);
    }
}
/**
 * 
 * @param {*} flag 
 */
const listenCopy = function (flag){
    window.addEventListener('copy', function(event){
        if(window.localStorage){
            let copyCount = Number(window.localStorage.getItem(STORAGE_COPY_COUNT))
            if(!copyCount || isNaN(Number(copyCount))){
                window.localStorage.setItem(STORAGE_COPY_COUNT, 1)
            }else{
                copyCount += 1
                window.localStorage.setItem(STORAGE_COPY_COUNT, copyCount)
            }
            if(copyCount > Config.COPY_COUNT){
                console.log('')
                window.localStorage.setItem(STORAGE_COPY_COUNT, 0)
            }
        }
        // 
        flag && additionData(event)
    })
}
module.exports = listenCopy
